@use "mixins";
.container {
    position: relative;
    @include mixins.maxWidth();
    display: grid;
    grid-template-rows: 300px 200px 60px;
    grid-template-columns: 15% 30% 55%;
    @media screen and (max-width: 756px) {
        grid-template-rows: 300px 200px 50px;
    }

    .restaurant {
        position: relative;
        grid-row: 1/3;
        grid-column: 2/4;
        @media screen and (max-width: 590px) {
            grid-column: 1/4;
            margin-left: 1.5rem;
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        .aboutUs {
            position: absolute;
            bottom: 0;
            right: -2rem;
            background: #342c24;
            padding: 2rem 1rem;
            @media screen and (max-width: 1600px) {
                right: 0;
            }
            h2 {
                color: var(--color-btn);
                font-family: "Architects Daughter", cursive;
                margin-bottom: 1.2rem;
                font-size: 1.6rem;
            }
            h3 {
                color: whitesmoke;
                font-size: 1.5rem;
                margin-bottom: 1rem;
            }

            p {
                font-size: 1.1rem;
                max-width: 400px;
                color: var(--color-text);
            }
        }
    }

    .bg1 {
        grid-row: 2/4;
        grid-column: 1/3;
        @media screen and (max-width: 540px) {
            grid-column: 1/4;
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}