@use "mixins";

.container {
    margin-top: 5rem;
    background: #1d1710;
    width: 100%;
}

.nav {
    @include mixins.maxWidth();
    padding: 2rem;
    display: flex;
    align-items: center;
    @media screen and (max-width: 960px) {
        flex-direction: column;
        align-items: flex-start;
        gap: 1.5rem;
    }

    .logoContainer {
        display: flex;
        align-items: center;
        .logo {
            font-family: "Architects Daughter", cursive;
            font-size: 2.4rem;
            color: var(--color-btn);
            cursor: pointer;
            transition: color ease 0.3s;
            &:hover {
                color: var(--color-text);
            }
        }

        .copyright {
            display: flex;
            background: var(--color-btn);
            font-size: 1rem;
            color: var(--color-text);
            margin-left: 3rem;
            white-space: nowrap;
            @media screen and (max-width: 540px) {
                font-size: 0.8rem;
                margin-left: 1rem;
            }
            @media screen and (max-width: 400px) {
                white-space: unset;
            }

            &:before {
                content: "";
                background: rgba(128, 128, 128, 0.637);
                width: 1px;
                padding-top: 1rem;
                margin-right: 0.5rem;
            }
        }
    }

    ul {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        gap: 1rem;
        width: 100%;
        @media screen and (max-width: 960px) {
            justify-content: unset;
        }
        @media screen and (max-width: 400px) {
            flex-direction: column;
        }
        li {
            color: var(--color-text);
            font-size: 1rem;
            cursor: pointer;
            white-space: nowrap;
            @media screen and (max-width: 540px) {
                font-size: 0.8rem;
            }
        }
        .socialsContainer {
            display: flex;
            align-items: center;
            white-space: nowrap;
            gap: 0.8rem;
            @media screen and (max-width: 400px) {
                flex-direction: column;
            }

            .socials {
                display: flex;

                svg {
                    width: 22px;
                    height: 22px;
                    fill: var(--color-text);
                    @media screen and (max-width: 540px) {
                        width: 17px;
                        height: 17px;
                    }
                }

                p {
                    color: var(--color-text);
                }
            }
        }
    }
}